.card {
  border: 1px solid #f3f3f3;
  padding: 20px;
  width: 170px;
  border-radius: 40px;
  margin-right: 30px;
  transition: box-shadow 0.2s ease-out, transform 0.2s ease-out;
  margin-bottom: 30px;

  .plus {
    cursor: pointer;
  }

  &:hover {
    box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.06);
    transform: translateY(-5px);
  }

  .favorite {
    position: absolute;
    cursor: pointer;
  }

  h5 {
    font-weight: 400;
    font-size: 14px;
  }

  .cardBottom {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .cardPrice {
      display: flex;
      flex-direction: column;
    }
  }

  span {
    font-size: 13px;
    opacity: 0.5;
    text-transform: uppercase;
  }

  b {
    font-size: 13px;
  }

  .button {
    background: #ffffff;
    border: 1px solid #f2f2f2;
    box-sizing: border-box;
    border-radius: 8px;
    height: 32px;
    width: 32px;
    cursor: pointer;
  }
}