body {
  margin: 0;
  font-family: 'Inter', system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e5b2b2;
}

* {
  font-family: Inter;
}

.wrapper {
  background-color: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
}

header {
  border-bottom: 1px solid #eaeaea;
  padding: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.headerLeft {
  display: flex;
  align-items: center;

  img {
    margin-right: 15px;
  }

  h3 {
    margin: 0;
    text-transform: uppercase;
  }

  p {
    margin: 0;
    opacity: 0.5;
  }
}

.headerRight {
  display: flex;
  cursor: pointer;

  li {
    list-style-type: none;

    img {
      margin: 0 20px 0 20px;

    }
  }
}

.content {
  padding: 40px;

  h1 {
    margin-bottom: 40px;
  }
}

.contentWrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}

.searchBlock {
  display: flex;
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0 15px;
  position: relative;

  .clear {
    position: absolute;
    right: 15px;
    width: 20px;
    height: 20px;
    top: 14px;

  }

  input {
    border: 0;
    padding: 13px;
    font-size: 16px;
    width: 200px;
  }
}

.sneakers {
  display: flex;
  flex-wrap: wrap;
}

.cardBottom {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .cardPrice {
    display: flex;
    flex-direction: column;
  }
}

.button {
  background: #ffffff;
  border: 1px solid #f2f2f2;
  box-sizing: border-box;
  border-radius: 8px;
  height: 32px;
  width: 32px;
  cursor: pointer;
}

  .cartEmpty {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;

    .emptyImage {
      margin-bottom: 20px;
      width: 120px;
    }

    p {
      width: 280px;
      line-height: 24px;
      opacity: 0.6;
    }

    .imgArrow {
      position: relative;
      top: 1px;
      transform: rotate(180deg);
      margin-right: 15px;
      transition: transform 0.15s ease-in-out;
    }

    .greenButton {
      position: relative;

      &:hover {
        img {
          transform: rotate(180deg) translateX(5px);
        }
      }
    }
  }

  .cartItemsBlock {
    display: flex;
    flex-direction: column;
    flex: 1;
  }

  .items {
    flex: 1;
    overflow: auto;
    margin-bottom: 40px;
  }

  h2 {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;

    img {
      cursor: pointer;
    }
  }


.cartItem {
  display: flex;
  align-items: center;
  border: 1px solid #f3f3f3;
  border-radius: 20px;
  overflow: hidden;
  margin-right: 20px;
  margin-bottom: 20px;

  img {
    margin-right: 20px;
  }

  .item {
    margin-right: 20px;

    p {
      margin: 0;
      font-size: 16px;
    }

    b {
      font-size: 14px;
    }
  }

  .cartItemRemove {
    opacity: 0.5;
    cursor: pointer;
    transition: opacity 0.15s ease-in-out;

    &:hover {
      opacity: 1;
    }
  }
}

.cartTotalBlock {
  ul {
    margin-bottom: 20px;

    li {
      display: flex;
      align-items: flex-end;
      margin-bottom: 20px;

      div {
        flex: 1;
        height: 1px;
        border-bottom: 1px dashed #dfdfdf;
        position: relative;
        top: -4px;
        margin: 0 7px;
      }
    }
  }

  .greenButton {
    position: relative;

    &:disabled {
      animation: button-loading 0.6s ease-in-out infinite;
    }

    &:hover {
      img {
        transform: translateX(5px);
      }
    }

    img {
      position: absolute;
      right: 30px;
      top: 20px;
      transition: transform 0.15s ease-in-out;
    }
  }
}

.greenButton {
  width: 100%;
  height: 55px;
  background: #9dd558;
  border-radius: 18px;
  border: 0;
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.1s ease-in-out;

  &:disabled {
    background-color: #7e796f !important;
    cursor: default;
  }

  &:hover {
    background: lighten(#9dd558, 10%);
  }

  &:active {
    background: darken(#9dd558, 5%);
  }
}
@keyframes button-loading {
  0% {
    opacity: 0.1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

